.display-panel{
    height: 85vh;
    border-style: solid;
    margin:20px;
    padding:30px;
    overflow: auto;
}

.input-field{
    height:8vh;
    font-size: 20px;
    margin-left:20px;
    line-height: 1;
    text-align: left;
    border-radius: 5px;
    resize: none;
}

.user-input-space{
    display: grid;
    grid-template-columns: 1fr 200px;
}

.submit-text{
    margin-left:10px;
    margin-right: 10px;
}

.content-container{
    display: flex;
}

.other-content{
    margin-right: auto;
    padding: 20px;
    border-style: solid;
    max-width: 40%;
    word-wrap: break-word;
    margin-bottom: 20px;
}

.user-content{
    margin-left: auto;
    padding: 20px;
    border-style: solid;
    max-width: 40%;
    word-wrap: break-word;
    margin-bottom: 20px;
}